/* Header Section */
.Main-Container{
    width:100%;
    height:100px;

}
header {
    text-align: center;
    width: 100%;
    display: flex;
    height: 110px;
    line-height: 15px;
    background-color: rgba(251, 159, 21, 1);
}

#name {
    font-size: 45px;
    
}

.name {
    float: right;
    width: 92%;
    height: 110px;
    /* border: solid red 2px; */
}

#logo {
    float: left;
    margin-left: 20px;
}
/* Header Responsive Mobile */
@media screen and (Max-width: 415px) {
    header {
        text-align: center;
        width: fit-content;
        display: flex;
        height: 90px;
        line-height: 15px;
        background-color: rgba(251, 159, 21, 1);
    }
    #name {
        font-size: 15px;
        font-weight:bold;   
    }
    h3{
        font-size:14px;
        font-weight:500;
    }
    .name {
        float: right;
        width: 92%;
        height: 110px;
    }
    #logo {
        float: left;
        margin-left:0px;
    }

  }
/* Header Section Ending */

/* Main Section  */
#MainImg{
    width:100%;
    height:500px;
    display: block;
}
.containerMain{
    height:100%;
    position: relative;
}
.one{
    position: absolute;
    top: 60px;
    color: white;
    font-size: 60px;
    left:80px;
}
.btn-two{
    position: absolute;
    left:80px;
    bottom: 180px;
    background-color:rgba(251, 159, 21, 1) ;
    color: black;
    font-weight: bold;
    width:150px;
    height:40px;
    border:none;
}

/* Main Mobile Responsive  */
@media screen and (Max-width: 415px) {

    #MainImg{
        width:100%;
        height:300px;
        display: block;
    }
.one{
    position: absolute;
    top: 60px;
    color: white;
    font-size: 25px;
    left:30px;
}
.btn-two{
    position: absolute;
    left:30px;
    bottom: 100px;
    background-color:rgba(251, 159, 21, 1) ;
    color: black;
    font-weight: bold;
    width:150px;
    height:40px;
    border:none;
}
}

/* Facility Section Starting */

#facility {
    text-align: center;
    font-weight: 700;
  }
  
  .facility-div {
    width: 100%;
    justify-content: center;
    margin-top: 15px;
    margin-bottom: 15px;
   
  }
  
  .facility-item {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 300px);
    align-items: center;
    justify-content: center;
    gap: 30px;
    text-align: center;
    margin: auto;
    padding: 50px;
    justify-content: space-around;
    background-color: rgba(227, 227, 227, 1);
  }

  #img {
  
    width: 203px;
    height: 130px;
    margin: auto;
  }
  
  .facility-item p {
    font-size: 6mm;
    font-weight: 700;
    margin-top: 0px;
    font-family: Arial, Helvetica, sans-serif;
  }
  .facility-item div {
    background-color: white;
    border-radius: 10px;
    /* border: 2px solid; */
    width: 290px;
    margin: auto;
  
  }

  /* Facility mobile responsive */

  @media screen and (Max-width: 415px) {
    .facility-item {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 150px);
        align-items: center;
        justify-content: center;
        /* gap: 30px; */
        text-align: center;
        margin: auto;
        /* padding: 50px; */
        justify-content: space-around;
        background-color: rgba(227, 227, 227, 1);
      }
      .facility-item div {
        background-color: white;
        border-radius: 10px;
        width: 150px;
        margin-left:0px !important;
      }
      #img {
        width: 100px;
        height: 70px;
        margin: auto;
      }
      .facility-item p {
        font-size:15px;
        font-weight: 700;
        margin-top: 0px;
        font-family: Arial, Helvetica, sans-serif;
      }
  }

  /* Counter Section Starting */
  .counter{
    background-color: rgba(255, 153, 0, 0.5);
    border-radius:10px;
    height:170px;
    width:290px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
   
  }

/* Counter  Mobile Responsive */
@media screen and (Max-width: 415px) {
  .counter{
    background-color: rgba(255, 153, 0, 0.5);
    border-radius:10px;
    height:150px;
    width:200px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top:10px;
   
  }

}
 